@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.App {
  text-align: center;
}

/* .App>:nth-child(2) {
  margin-top: 80px;
} */

.error {
  background: #c55;
  color: white;
  padding: 20px 100px;
}

.form-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-background {
  height: 60vh;
  width: 100%;
  background-image: linear-gradient(rgba(45, 1, 1, 0.49), rgba(45, 1, 1, 0.49)),
    url("https://www.reviews.org/app/uploads/2018/06/Family-watching-YouTube-TV-on-laptop-e1573858678412.jpg");
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.main-page-text {
  text-align: center;
  margin: 0;
}

.SearchForm {
  width: 100%;
}

.SearchForm * {
  font-size: 1.5rem;
}

.SearchForm input {
  margin-right: 10px;
  width: 75%;
  height: 100%;
  padding-left: 0.5rem;
}

.SearchForm button {
  padding: 0 1%;
  background-color: #b61239;
  color: white;
  border-radius: 5px;
  border: none;
  height: 100%;
}

.text-container {
  color: #fff;
  position: relative;
  margin-bottom: 2%;
  padding: 1rem;
}

.main-page-text {
  font-size: 2rem;
  font-family: "Roboto", sans-serif;
}

.main-page-p {
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
}

@media all and (min-width: 672px) {
  .SearchForm input {
    margin-right: 10px;
    width: 25%;
    height: 100%;
    padding-left: 0.5rem;
  }
  .main-page-text {
    font-size: 3rem;
  }

  .main-page-p {
    font-size: 1.5rem;
  }
}

header {
  background-color: #b61239;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  position: static;
  width: 100vw;
  top: 0;
  z-index: 100;
}

.homepage-link {
  height: 100%;
}

.now-what-text {
  display: inline-block;
  color: #fff;
  text-align: center;
  font-size: 2rem;
  font-family: "Permanent Marker", cursive;
  font-weight: 300;
}

.now-what-button {
  background-color: #b61239;
  border: none;
  cursor: pointer;
  padding: 0 2%;
  height: 100%;
}

.title-section {
  height: 100%;
  width: 25%;
  min-width: 200px;
  max-width: 300px;
  margin: 0;
  text-align: left;
}

.favorites {
  letter-spacing: "-1px";
  color: white;
  font-family: "Lato", sans-serif;
}

.header-search {
  font-size: 1.5rem;
  height: 2rem;
  display: inline-block;
  margin-right: 20px;
  border-radius: 5px;
}

.header-search-button {
  border-radius: 5px;
  font-size: 1.5rem;
  height: 2rem;
  width: 120px;
  margin-right: 10px;
  background-color: #cbe5e7;
}

.actions {
  margin-right: 5px;
  display: flex;
  align-items: center;
  height: 100%;
}

.actions > * {
  margin-right: 10px;
}

.navlink {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  color: white;
}

.navlink:hover {
  background: black;
  color: #d3d3d3;
}

.header-form {
  position: absolute;
  background: #b61239;
  top: 60px;
  right: 0px;
  margin-right: 0px;
  padding-top: 5px;
  height: 0;
  transform: scaleY(0);
  transform-origin: 50% 0%;
  transition: transform 0.3s, height 0.3s step-end;
}

.header-form.show {
  height: 40px;
  width: 100%;
  transform: scaleY(1);
  transition: transform 0.3s, width 0s;
}

@media all and (min-width: 600px) {
  .header-form {
    position: static;
    height: 40px !important;
    width: 0px;
    transform: scaleX(0);
    transform-origin: 100% 50%;
    transition: transform 0.3s, width 0.3s step-end;
  }
  .header-form.show {
    width: 100%;
    transform: scaleX(1);
    transition: transform 0.3s, width 0s;
  }
}

.Result {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 200px;
  position: relative;
  background-color: #EEEEEE;
  margin: 2%;
  padding: 1%;
  border-radius: 5px;
}

.Result h3 {
  max-width: 130px;
  text-align: left;
}

.result-card-image {
  width: 140px;
  height: 200px;
  background: none;
  object-fit: contain;
  margin: 5% 0;
}

.Result .FavoriteButton {
  position: absolute;
  bottom: 2%;
  right: 5%;
}
.FavoriteButton {
  border: none;
  background: none;
}

.FavoriteButton * {
  pointer-events: none;
}

.favorite * {
  fill: white;
}

.favorite.toggled * {
  fill: red;
}
.ResultsPage {
  background: #473e40;
  border-radius: 5px;
  width: 90%;
  margin: 0 auto;
}

.search-item-display {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search-item-display h2 {
  text-align: center !important;
}

.ResultsPage h2 {
  width: 100%;
  text-align: left;
  margin: 1% 2%;
  color: white;
}

.results-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.favorites-display {
    display: flex;
    flex-wrap: wrap;
    background-color: #473E40;
    margin: 0 3%;
    border-radius: 5px;
}

.favorites-header {
    width: 100%;
    text-align: left;
    margin: 1% 3%;
    font-family: "Avenir Next";
    font-weight: bold;
  }

  .favorites-section {
      width: 100%;
      margin: 1%;
      display: flex;
      flex-wrap: wrap;
  }

   .favorite-type-header {
        color: #EEEEEE;
        margin: 2% 1%;
        font-family: "Avenir Next";
        width: 100%;
        text-align: left;
        font-size: 1.5rem;
   } 

.title-page {
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 90%;
  margin: 2% auto;
  background: #473e40;
  border-radius: 5px;
  color: white;
  overflow: scroll;
}

.title-page a {
  color: #88e;
}

.title-page a:visited {
  color: #a7a;
}

.title-title {
  font-family: "Avenir Next", Helvetica, Arial, sans-serif;
  font-weight: 300;
  line-height: 28px;
  max-width: 80%;
}

.title-overview {
  margin: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: space-between; */
}

.title-favorite-button {
  position: relative;
  margin: 0;
}

.title-detailed-info {
  margin: 0% 5% 5%;
  text-align: left;
}

.title-detailed-info h3 {
  font-family: "Avenir Next", Helvetica, Arial, sans-serif;
  font-weight: 300;
}

.wiki-link {
  font-style: italic;
  margin: 2% 0;
  color: white;
}

.wiki-link:visited {
  color: white;
}

.title-image {
  height: 400px;
  margin-top: 10px;
  width: auto;
  max-width: 250px;
  object-fit: contain;
}

.youtube-link {
  height: 100px;
  width: 100px;
}

.all-related-items {
  display: flex;
  flex-direction: row;
  align-content: center;
  margin: 2%;
  padding: 1%;
  overflow: scroll;
}

.title-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: 100%;
}

.title-header .FavoriteButton {
  background: white;
  width: 30px;
  height: 30px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 3px;
}

@media all and (min-width: 600px) {
  .title-page {
    flex-direction: row;
    width: 80%;
    height: 95%;
  }
  .title-overview {
    width: 30%;
  }
  .title-detailed-info {
    width: 70%;
    margin: 5%;
    overflow: scroll;
  }
}

.related-item {
  margin: 1%;
  height: 100%;
  background: white;
  border-radius: 5px;
}

.related-item-title {
  margin: 6px;
  text-align: center;
}

.related-item img {
  height: 100px;
  width: 200px;
  object-fit: cover;
  margin: 5px;
}

.all-related-items a .related-item {
  color: #222;
}

.all-related-items a:visited .related-item {
  color: #555;
}
.Editors-Choice {
  display: flex;
  flex-wrap: wrap;
  overflow: scroll;
  margin: 1% 2%;
  background-color: #c4c4c4;
  border-radius: 5px;
}

.editor-pick-section {
  width: 100%;
  margin: 1%;
  display: flex;
  flex-wrap: wrap;
  /* background-color: #C4C4C4; */
}

.editor-pick-section-header {
  color: black;
  margin: 1%;
  font-family: "Avenir Next", sans-serif;
  width: 100%;
  text-align: left;
  font-size: 1.5rem;
  text-transform: capitalize;
}

